import React, { Component } from 'react';
import http from './http'

export default {

    //查询列表数据处理
    selectListDataProcessing(res){
        res.data.pageSize = res.data.size;
        res.data.size = null;
    },

    //查询应用列表
    selectAppList(data){
        return http.post("aptsApp/selectAppList",data);
    },
    
    //保存应用
    saveApp(data){
        return http.post("aptsApp/saveApp",data);
    },

    //删除应用
    deleteApp(data){
        return http.post("aptsApp/deleteApp",data);
    },
    
    //查看应用
    getApp(data){
        return http.post("aptsApp/getApp",data);
    },
    
    //启动应用
    startApp(data){
        return http.post("aptsApp/startApp",data);
    },

    //停止应用
    stopApp(data){
        return http.post("aptsApp/stopApp",data);
    }
}