import React, { Component } from 'react';
import { Table,Input,Button,Icon,message,Modal,Form,Upload } from 'antd';
import api from '../http/api'
import './App.less'
import msg from '../http/msg';
import http from '../http/http'
const Search = Input.Search;

const confirm = Modal.confirm;
const FormItem = Form.Item;
const formItemLayout = {
    labelCol: {
      xs: { span: 10 },
      sm: { span: 8 },
    },
    wrapperCol: {
      xs: { span: 10 },
      sm: { span: 10 },
    },
  };
const heanders = {token:"cnm"}
export default class extends Component{
    state = {
        pagination:{
            records:[],
        },
        loading:false,
        appList:{
            current:1,
            size:10,
            name:''
        },
        app:{
            id:"",
            name:"",
            fileName:""
        },
        modal:{
            title:""
        },
        wsList:[]
    }
    handleChange = (info) => {
        let fileList = info.fileList;

        // 1. Limit the number of uploaded files
        // Only to show two recent uploaded files, and old ones will be replaced by the new
        fileList = fileList.slice(-1);

        // 2. Read from response and show file link
        fileList = fileList.map((file) => {
        if (file.response) {
            // Component will show file.url as link
            // file.url = file.response.url;
            file.name = file.response.data[0];
        }
        return file;
        });

        // 3. Filter successfully uploaded files according to response from server
        fileList = fileList.filter((file) => {
            if (file.response) {
                return file.response.code === 200;
            }
            return true;
        });
        
        this.setState({ app:{id:this.state.app.id,fileName:fileList,name:this.state.app.name} });
      }
    constructor(props){
        super(props);
    }
    /**
     * 渲染后调用
     */
    componentDidMount(){
        this.selectAppList()
    }

    selectAppList = () => {
        this.setState({loading:true});
        let result = api.selectAppList(this.state.appList);
        result.then(res=>{
            if(res.code==200){
                api.selectListDataProcessing(res);
                this.setState({
                    loading:false,
                    pagination: res.data,
                })
            }
        })
    }

    printState = (state) => {
        if(state==0){
            return <span><Icon type="play-circle" /> 待运行</span>
        }else if(state == 1){
            return <span><Icon type="loading" /> 启动中</span>
        }else if(state == 2){
            return <span><Icon type="pause-circle" /> 已运行</span>
        }
    }

    //操作
    printOperation = (record) => {
        let state = record.state;
        let id = record.id;
        if(state == 1){
            let flag = true;
            this.state.wsList.map((item)=>{
                if(item == id){
                    flag = false;
                }
            })
            if(flag){
                this.state.wsList.push(id);
                var ws = new WebSocket(http.wsUrl+"/aptsAppWs/" + id);
                let self = this;
                ws.onmessage = function (evt) 
                { 
                    let received_msg = JSON.parse(evt.data);
                    if(received_msg.code == 200){
                        record.state = 2;
                        self.setState({appList:self.state.appList});
                        msg.msg({code:200,message:record.name+" 已成功运行"});
                        this.close();
                    }else if(received_msg.code == 400){
                        record.state = 0;
                        self.setState({appList:self.state.appList});
                        msg.msg({code:400,message:record.name+" 运行失败"});
                        this.close();
                    }
                    let list = self.state.wsList;
                    for (let i = 0; i < list.length; i++) {
                        const item = list[i];
                        if(item == id){
                            list.splice(i,1);
                            break;
                        }
                    }
                };
            }
        }
        let edit = <span className="ad">编辑</span>;
        let start = <span className="ad">运行</span>;
        let stop = <a className="aptsA" onClick={() => this.appStop(id,record)}>停止</a>;
        let del = <span className="ad">删除</span>;
        if(state==0){
            edit = <a className="aptsA" onClick={() => this.appEdit(id)}>编辑</a>;
            start = <a className="aptsA" onClick={() => this.appStart(id,record)}>运行</a>;
            stop = <span className="ad" >停止</span>;
            del = <a className="aptsA" onClick={() => this.appDel(id)}>删除</a>;
        }
        return (
            <span>
                {edit}
                <span> | </span>
                {start}
                <span> | </span>
                {stop}
                <span> | </span>
                {del}
                <span> | </span>
                <a className="aptsA" onClick={()=> this.selectLog(id)}>查看实时日志</a>
            </span>
        )
    }

    //查看日志
    selectLog = (id) => {
        window.open("/#/log/" + id);
    }

    //编辑
    appEdit = (id) => {
        this.showModal(1,id);
    }

    //启动
    appStart = (id,record) => {
        let result = api.startApp({id});
        result.then(res=>{
            if(res.code == 200){
                record.state = 1;
                this.setState({appList:this.state.appList})
                
            }
        })
    }
    //停止
    appStop = (id,record) => {
        let result = api.stopApp({id});
        result.then(res=>{
            if(res.code == 200){
                record.state = 0;
                this.setState({appList:this.state.appList})
            }
        })
    }

    appDel = (id) => {
        let self = this;
        confirm({
            title: '确定要删除此应用?',
            content: '一旦删除无法恢复',
            okText: '删除',
            okType: 'danger',
            cancelText: '取消',
            onOk() {
              let result = api.deleteApp({id});
              result.then(res=>{
                  if(res.code == 200){
                    self.selectAppList();
                  }
              })
            },
            onCancel() {
              console.log('Cancel');
            },
          });
    }

    columns = [{
        title:'应用名',
        dataIndex:'name',
    },{
        title:'项目文件',
        dataIndex:'fileName',
    },{
        title:'应用状态',
        dataIndex:'state',
        render:(text,record) =>( 
            <span>
                {this.printState(text)}
            </span>
        )
    },{
        title:'创建时间',
        dataIndex:'createTime'
    },{
        title:'操作',
        dataIndex:'state',
        dataIndex:'id',
        render:(text,record) => (
            <span>
                {this.printOperation(record)}
            </span>
        )
    }];
    showModal = (status,id) => {
        if(status == 0){
            this.state.modal.title = "添加应用";
        }else if(status == 1){
            this.state.modal.title = "修改应用";
            let result = api.getApp({id});
            result.then(res=>{
                if(res.code==200){
                    let data = res.data;
                    let fileList = [{
                        uid: '1',
                        name: data.fileName,
                        status: 'done',
                        response: 'Server Error 500', // custom error message to show
                        url: '',
                      }]
                    let app = {
                        id:data.id,
                        name:data.name,
                        fileName:fileList
                    }
                    this.setState({app})
                }
            });
        }
        this.setState({
            visible: true,
        });
    }
    handleOk = (e) => {
        if(this.state.app.name == null || this.state.app.name == ''){
            msg.msg({code:400,message:"请编辑应用名称"})
            return;
        }
        if(this.state.app.fileName.length < 1){
            msg.msg({code:400,message:"请上传文件"})
            return;
        }
        let app = {
            id:this.state.app.id,
            name:this.state.app.name,
            fileName:this.state.app.fileName[0].name
        };
        let result = api.saveApp(app);
        result.then(res=>{
            if(res.code == 200){
                this.selectAppList();
                this.setState({
                    visible: false,
                    app:{
                        id:"",
                        name:"",
                        fileName:""
                    }
                });
            }
        })
    }
    handleCancel = (e) => {
        this.setState({
            visible: false,
            app:{
                id:"",
                name:"",
                fileName:""
            }
        });
    }
    sousuo = (value) =>{
        this.state.appList.name = value;
        this.selectAppList();
    }

    handleTableChange = (pagination, filters, sorter) => {
        this.state.appList.current = pagination.current;
        this.selectAppList();
    }

    handleInput = (e) => {
        this.setState({
            app:{
                id:this.state.app.id,
                name:e.target.value,
                fileName:this.state.app.fileName
            }
        });
    }
    render(){
        return (
            <div>
                <div style={{display:'flex',justifyContent:'space-between' }}>
                <Search
                    placeholder="搜索"
                    onSearch={this.sousuo}
                    style={{ width: 200,marginBottom:'15px' }}
                />
                    <Button type="primary" ghost onClick={()=>this.showModal(0)}><Icon type="plus"/>添加应用</Button>
                </div>
                <Table 
                    columns={this.columns} 
                    dataSource={this.state.pagination.records} 
                    pagination={this.state.pagination}
                    loading={this.state.loading}
                    onChange={this.handleTableChange}
                    size="small" 
                    // rowKey="fileName"
                />
                <Modal
                title={this.state.modal.title}
                visible={this.state.visible}
                onOk={this.handleOk}
                onCancel={this.handleCancel}
                cancelText="取消"
                okText="确定"
                >
                <Form onSubmit={this.handleSubmit}>
                    <FormItem
                    {...formItemLayout}
                    label="应用名称："
                    >
                        <Input onChange={this.handleInput} value={this.state.app.name}/>
                    </FormItem>
                    <FormItem
                    {...formItemLayout}
                    label="项目文件："
                    >
                        <Upload onChange={this.handleChange} fileList={this.state.app.fileName}
                        action='/aptsApp/uploadAppFile'
                        headers={heanders}
                        >
                            <Button>
                            <Icon type="upload" /> 上传jar包
                            </Button>
                        </Upload>
                    </FormItem>
                </Form>
                </Modal>
            </div>
        );
    }
}