import axios from 'axios'
import qs from 'qs'
import msg from './msg';

// axios.defaults.baseURL = 'http://139.196.98.45:8899';
axios.defaults.timeout = 100000

export default {

    wsUrl:'ws://'+window.location.host,

    post(url,data) {
        return new Promise((resolve, reject) => {
            axios.post(url, qs.stringify(data), {
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                }
            }
            ).then(res => {
                msg.msg(res.data)
                resolve(res.data);
            }).catch(err => {
                msg.msg({
                    code:500
                })
                reject(err)
            })
        })
    },

    get(url, params) {
        return new Promise((resolve, reject) => {
            axios.get(url, {
                params: params
            }).then(res => {
                resolve(res.data);
            }).catch(err => {
                reject(err);
            })
        })
    }
    
}