import React, { Component } from 'react';
import './Home.less';
import {Link,Route} from 'react-router-dom'
import App from './App/App'
import {
    Layout, Menu, Breadcrumb, Icon,
  } from 'antd';
const {
Header, Content, Footer, Sider,
} = Layout;
const SubMenu = Menu.SubMenu;
  
export default class extends Component{
    constructor(props){
        super(props);
        this.state = {
            collapsed:false
        }
    }
    onCollapse = (collapsed) => {
        console.log(collapsed);
        this.setState({ collapsed });
    }

    render(){
        return (
            <Layout style={{ minHeight: '100vh' }}>
        <Sider
          collapsible
          collapsed={this.state.collapsed}
          onCollapse={this.onCollapse}
        >
          <div className="logo" />
          <Menu theme="dark" defaultSelectedKeys={['1']} mode="inline">
            <Menu.Item key="1"><Link to="/app"><Icon type="pie-chart" />应用管理</Link></Menu.Item>
            <Menu.Item key="2"><Link to="/app"><Icon type="pie-chart" />全局配置</Link></Menu.Item>
            <Menu.Item key="3"><Link to="/app"><Icon type="pie-chart" />工具配置</Link></Menu.Item>
            <Menu.Item key="4"><Link to="/app"><Icon type="pie-chart" />用户管理</Link></Menu.Item>
            <Menu.Item key="5"><Link to="/app"><Icon type="pie-chart" />个人中心</Link></Menu.Item>
          </Menu>
        </Sider>
        <Layout>
          {/* <Header style={{ background: '#fff', padding: 0 }} /> */}
          <Content style={{ margin: '16px 16px 0px 16px' }}>
            {/* <Breadcrumb style={{ margin: '16px 0' }}>
              <Breadcrumb.Item>User</Breadcrumb.Item>
              <Breadcrumb.Item>Bill</Breadcrumb.Item>
            </Breadcrumb> */}
            <div style={{ padding: 24, background: '#fff', minHeight: '85vh' }}>
              <Route path="/app" component={App} />
            </div>
          </Content>
          <Footer style={{ textAlign: 'center' }}>
            
          </Footer>
        </Layout>
      </Layout>
        );
    }
}