import {message} from 'antd'

export default {
    success:200,
    fail:400,
    error:500,
    msg(data){
        if (data.code == 200) {
            message.success(data.message);
        }else if(data.code == 400) {
            message.error(data.message);
        }else {
            message.error("系统异常")
        }
    }
}